.project-card {
  background-color: #0e0e0e;
  border: 0.9px solid #222222;  
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease-in-out;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}


.project-card h3 {
  padding: 15px;
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 1.5em;
  text-align: center;
  color: #fff;
}

.project-card p {
  padding: 0 20px;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 1.1em;
  color: #83858D;
  line-height: 1.5;
  height: 50%;
}

.project-card .project-links {
  padding: 15px;
  text-align: center;
  border-top: 0.9px solid #222222; /* Questo bordo superiore è rimasto lo stesso */
}

.project-card .project-links a {
  background-color: #0e0e0e;
  margin: 5px 10px;
  padding: 10px 15px;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  font-size: 0.9em;
  transition: background-color 0.3s ease-in-out, color 0.2s ease-in-out;
  border-radius: 10px;
  border: 1px solid #555; /* Assicurati che questo bordo sia come lo desideri, non è stato modificato */
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-card .project-links a:hover {
  background-color: #6210E5;
  color: white;
  border-color: #6210E5;
}

.project-card .project-info {
  padding: 20px;
  text-align: center;
}

.project-card-image {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-bottom: 0.9px solid #222222;
  height: 250px;
  width: auto;
}

.project-card-image #app-meteo {
  width: 100%; /* L'immagine riempie il 100% della larghezza del suo contenitore (.project-card-image) */
  height: 100%; /* L'immagine riempie il 100% dell'altezza del suo contenitore */
  object-fit: cover; /* **LA SOLUZIONE**: Scala l'immagine per coprire l'intero contenitore mantenendo le proporzioni. Le parti in eccesso verranno tagliate, creando l'effetto "anteprima" che riempie. */
  display: block; /* Rimuove lo spazio bianco sotto l'immagine che a volte si crea */
  object-position: center 0px; 
}

